// https://hexo.io/zh-cn/docs/configuration.html

export class HexoConfig {
  site: Site = new Site();
  url: URL = new URL();
  directory: Directory = new Directory();
  writing: Writing = new Writing();
  categoriesAndTags: CategoryAndTags = new CategoryAndTags();
  dateTimeFormat: DateTimeFormat = new DateTimeFormat();
  page: Pagination = new Pagination();
  extensions: Extensions = new Extensions();

  constructor(raw?: any) {
    if (raw) {
      this.site = new Site(raw);
      this.url = new URL(raw);
      this.directory = new Directory(raw);
      this.writing = new Writing(raw);
      this.categoriesAndTags = new CategoryAndTags(raw);
      this.dateTimeFormat = new DateTimeFormat(raw);
      this.page = new Pagination(raw);
      this.extensions = new Extensions(raw);
    }
  }
}

export class Site {
  title = ''; // 
  subtitle = ''; // 
  description = ''; // 
  author = ''; // 
  language = ''; // 
  timezone = ''; //Hexo America/New_York, Japan,  UTC 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class URL {
  url = ''; // 
  root = ''; // 
  permalink = ''; //   
  permalink_defaults = ''; // 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class Directory {
  source_dir = ''; // 
  public_dir = ''; // 
  tag_dir = ''; // 
  archive_dir = ''; // 
  category_dir = ''; // 
  code_dir = ''; // Include code 
  i18n_dir = ''; // i18n
  skip_render = ''; //  glob 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class Writing {
  new_post_name = ''; // 
  default_layout = ''; // 
  titlecase = false; //  title case
  filename_case = 0; //  (1)  (2) 
  external_link = ''; // 
  render_drafts = false; // 
  post_asset_folder = false; //  Asset 
  relative_link = false; // 
  future = true; // 
  highlight: {
    enable: boolean,
    line_number: boolean,
    auto_detect: boolean,
    tab_replace: string
  } | null = null; // 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class CategoryAndTags {
  default_category = ''; // 
  category_map = ''; // 
  tag_map = ''; // 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class DateTimeFormat {
  date_format = ''; // 	YYYY-MM-DD
  time_format = ''; // 	H:mm:ss
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class Pagination {
  per_page = 0; //  (0 = )
  pagination_dir = ''; // 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}

export class Extensions {
  theme: string | boolean = false; // false
  deploy = {}; // 
  constructor(raw?: any) {
    if (raw) {
      for (const key of Object.keys(this)) {
        if (raw.hasOwnProperty(key)) {
          Object.assign(this, { [ key ]: raw[ key ] });
        }
      }
    }
  }
}




